@use '../globals/colors' as *;

.body {
    position: relative;
}

header {
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    background-color: rgba(0 0 0 / 0.5);
    color: hsl(0, 0%, 85%);
    z-index: 500;
    line-height: 1;
    font-weight: 300;
    font-size: clamp(1rem, 2vw, 1.5rem);
    padding-inline: 3vw;
    box-shadow: 0px 10px 19px -8px rgba(0,0,0,0.75);


    .main-nav {
        flex-grow: 1;
    }

    .nav__list {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        
        .nav__link {
            flex: 1;
        }
    }


    

    .nav__link {
            text-decoration: none;
            display: inline-block;
            font-weight: 400;
            color: inherit;
            padding: 2rem 0;
            text-align: center;
            
            align-self: center;
            transition: 250ms;
        

            &:hover {
                background-color: var(--accent-color);
                color: var(--prim-color);
            }
    }
   
    
     
}



#logo-name {
    display: none;

    flex-shrink: 0;
    margin-right: 1em;
    text-align: center;
    font-size: 2rem;
    font-weight: 800;
    
    line-height: 1;
    
    a {
        text-decoration: none;
        color: var(--accent-color);
    }
    
    span {
        color: black;
        font-size: 0.75rem;
        font-weight: 300;
        display: block;
    }
}