#about {
    min-height: 100vh;
    position: relative;
    // overflow: hidden;
}

.intro {
    font-weight: 300;
    font-size: 1.75rem;
    line-height: 1.5;
    background-color: var(--accent-color);
    padding: 1rem 2rem;
    position: absolute;
    max-width: 35rem;
    top: 25rem;

    span {
        font-weight: 800;
        color: var(--bg-color);
    }
}

.about-1 {
    position: absolute;
    z-index: -1;
    width: 50rem;
    right: 0;
}

.about-2 {
    position: absolute;
    z-index: -1;
    transform: translateY(100%);
}


.add-about {
    bottom: 0;
    position: absolute;
    transform: translateY(100%);
}