@use 'globals/colors' as *;

section:not(:first-of-type) {
    max-width: 80rem;
    margin: 0 auto;
}

.main-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    
    @media (max-width: 60em) {
        grid-template-columns: 1fr;
        margin-inline: .5em;
    }
}

.card {
    aspect-ratio: 1 / 1;
    position: relative;
    overflow: hidden;

    &::after {
        content: '';
        z-index: -1;
        position: absolute;
        inset: 0;
        background-color: rgb(46, 100, 79);
        opacity: 0.5; 
    }
    
    &-img {
        position: absolute;
        z-index: -2;
        object-fit: cover;
        height: 100%;
        width: 100%;
        transition: transform 400ms ease-in;
        
    }

    &-name {
        font-weight: 800;
        font-size: 3rem;
        position: absolute;
        top: 1rem;
        left: 1rem;
        transition:  400ms ease-in;
    }

    &-desc {
        position: absolute;
        left: 1rem;
        top: 5rem;
        color: transparent;
        transition:  400ms ease-in 100ms;
    }

}

.card:hover {
    .card-name {
        font-size: 2rem;
        // transform: translateY(-700%);
    }

    .card-img {
        transform: scale(120%);
    }

    .card-desc {
        color: var(--prim-color);
        // transform: translateY(-100%);
    }

    
}

.card:hover.card::after {
    opacity: 0.9;
}